import React from 'react';
import './App.css';
import Board from './pages/Board';
import { Route, Switch, BrowserRouter } from 'react-router-dom';

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Switch>
          <Route path="/" component={Board} />
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default App;
